text {
  font-family: "Benton Sans", Helvetica, Arial, sans-serif !important;
}

.background {
  fill: none;
  pointer-events: all;
}

.states {
  fill: #aaa;
}

#state-borders {
  fill: none;
  stroke: #fff;
  stroke-width: 2px;
  stroke-linejoin: round;
  stroke-linecap: round;
  pointer-events: none;
}

.tooltip {
  font-family: "Benton Sans", Helvetica, Arial, sans-serif;
  background-color: white;
  height: auto;
  width: auto;
  padding: 10px;
  border-radius: 0;
  border: 1px solid black;
  font-size: 13px;
  font-family: Arial;
}

.states .active {
  fill: #333 !important;
  fill-opacity: 1 !important;
}

.faded {
  fill-opacity: 0.5 !important;
}

.states path:hover {
  fill: #333 !important;
  cursor: pointer;
}